import {React,useState} from 'react'
import {SliderData2}  from './SliderData2';
import {FaArrowAltCircleRight,FaArrowAltCircleLeft } from 'react-icons/fa';
import './CSS/Slider.css';

 
function Slider ({slides})  {
    const[currentValue, setCurrentValue] = useState(0);
    const length = slides.length;

    const nextSlide = () =>{
        setCurrentValue( currentValue === length-1 ? 0 : currentValue+1);
    }

    const prevSlide = () =>{
        setCurrentValue( currentValue === 0 ? length-1 : currentValue-1);
    }

    console.log('Valor Actual: '+currentValue);

    if(!Array.isArray(slides) || slides.length<=0){
        return null;
    }
    return (
        <>
        <br/><h1>Muestra de nuestro trabajo</h1><br/>
        <section className="slider">
        <FaArrowAltCircleLeft className="left-arrow" onClick={prevSlide} />
        <FaArrowAltCircleRight className="right-arrow" onClick={nextSlide}/>
          {SliderData2.map((slide, index) =>{
              //return <img src={require(slide.image)} alt="trabajos"/>; 
              return (
                  <div className={index === currentValue ? 'slide active': 'slide'} key={index}>
                      {index === currentValue && (<img src={slide.image} alt="Trabajos" className="images" />)}
                      
                  </div>
              );
              //return <img src={require( `${ slide.image }` )} alt="Trabajos" className="images" />;
          })}
          
        </section>
        </>
    );
}

export default Slider
