import React from 'react'
import './CSS/Footer.css'
import {Link} from 'react-router-dom'

function Footer() {

   


    return (
        <div className="footer-container">
            <div className="footer-links">
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <Link to="/services"><h2>Servicios</h2></Link>
                    </div>
                </div>     
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                    <Link to="/contacts"><h2>Contactos</h2></Link>
                    </div>
                </div>                
            </div>
            
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="footer-logo">
                        <Link to="/" className="social-logo">
                            PCP <i class="fas fa-water"></i>
                        </Link>
                    </div>
                    <small className="website-rights">PCP 2021</small>
                    <div className="social-icons">
                        <a className="social-icon-link facebook"
                        href="https://www.facebook.com/Proyectos-de-Climatizaci%C3%B3n-y-Piscinas-102627125545712"
                        target='_blank'
                        aria-label='Facebook'
                        rel="noopener noreferrer"
                        >
                            <i class="fab fa-facebook-f"></i></a>
                        <a className="social-icon-link instagram"
                        href="https://www.instagram.com/proyectosclimatizacion/"
                        target='_blank'
                        aria-label='Instagram'
                        rel="noopener noreferrer"
                        >
                            <i class="fab fa-instagram"></i></a>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Footer
