import {React,useState,useLayoutEffect} from "react";
import '../../App.css'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import '../CSS/Form.css';
import axios from 'axios';


export default function Contacts(){
    useLayoutEffect(() => {
        if(formValue.email.length===0 && formValue.name.length===0 && formValue.message.length===0  )
        window.scrollTo(0, 0)
    });
    
    const [formValue, setFormValue] = useState({
        email: "",
        name: "",
        message: "",
      });
    const [disabled, setDisabled] = useState(false);
    const [emailSent, setEmailSent] = useState(null);

    const handleChange = (event) =>{
        const {name,value} = event.target
        setFormValue((prevState) => {
            return {
              ...prevState,
              [name]: value,
            };
          });
        };
    
    const {email,name,message} = formValue;

    const headers = {
        "Content-Type": "application/json",
        "Accept": "application/json",
      }
    const handleSubmit = (event) =>{
        event.preventDefault();
        setDisabled(true);
        axios.post('/api/mail', formValue,{headers:headers})
        .then(res =>{
            if(res.data.success){
                setDisabled(false); 
                setEmailSent(false);
            }else{
                setDisabled(false);
                setEmailSent(true);
                resetForm();
            }
            

        }).catch( error =>{
            setDisabled(false); 
            setEmailSent(false);
        
        })
        
    };

    const resetForm = () =>{
        setFormValue({
            email: "",
            name: "",
            message: "",
          });
       console.log('Se borro el formulario');

        
    }
    return (
        <>
        <h1 className="contacts">Contactos</h1>
        <div className="contact-section">
           <div className="info-section">
           <div className="column text">
                    <h2>Proyectos de Climatización y Piscinas</h2>
                    <p>Passeig de l'Estació, 2, 08630 Abrera</p>
                    <p>Barcelona, España</p>
                    <p><strong>Teléfono:</strong> +34 937738312</p>
                    <p><strong>Movil:</strong> +34 631 63 13 94</p>
                    <p><strong>Email:</strong> pclimatizacionpiscinas@gmail.com</p>
                    <p>Escribenos al Whatsapp <i class="fab fa-whatsapp"></i>: <a href='https://wa.me/34631631394' target="_blank" rel="noreferrer noopener">Click Aqui</a></p>
                </div>
                
                <div className="column">
                    <iframe title ="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2987.252680888354!2d1.897134315429929!3d41.52046697925214!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0!2zNDHCsDMxJzEzLjciTiAxwrA1Myc1Ny42IkU!5e0!3m2!1ses-419!2sec!4v1634664581112!5m2!1ses-419!2sec" className="map" allowFullScreen="" loading="lazy"></iframe>
                </div>
            
           </div>
          <div className="form-section">
              <p>Si tiene alguna duda escribanos a nuestro mail, escribenos estaremos encantado de resolverla</p>
            <Form onSubmit={handleSubmit}>
                    <Form.Group>
                        <Form.Label htmlFor="full-name">Nombre Completo</Form.Label>
                        <Form.Control id="full-name" name="name" type="text" value={name} onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label htmlFor="email">Email</Form.Label>
                        <Form.Control id="email" name="email" type="email" value={email} onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label htmlFor="message">Mensaje</Form.Label>
                        <Form.Control id="message" name="message" as="textarea" rows="3" value={message} onChange={handleChange}/>
                    </Form.Group>

                    <Button className="d-inline-block button" variant="primary" type="submit" disabled={disabled}>
                        Enviar
                    </Button>

                    {emailSent === true && <p className="d-inline success-msg">Email Enviado</p>}
                    {emailSent === false && <p className="d-inline error-msg">Email No Enviado</p>}
                </Form>
          </div>
        </div>
        </>
    );
}