import React from 'react'
import '../App.css'
import {Button} from './Button';
import './CSS/Hsection.css';

function Hsection() {
    return (
        <div className="hero-container">
            <h1>PCP</h1>
            <p>Proyectos de Climatización y Piscinas</p>
            <div className="hero-btns">
                <Button className='btns' buttonStyle='btn--outline' buttonSize='btn--large'>¿Quienes Somos?</Button>
            </div>
        </div>
    )
}

export default Hsection
